body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

.catalog {
  flex-direction: column;
  width: 100%;
  display: flex;
}

.nav {
  text-transform: uppercase;
  background: #fff;
  justify-content: flex-end;
  align-items: center;
  gap: 20px;
  height: 60px;
  font-size: 12px;
  display: flex;
}

.nav :last-child {
  margin-right: 50px;
}

.nav__link {
  color: #060b35;
  text-decoration: none;
}

.is-active {
  color: #00acdc;
  padding: 22.5px 0;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.img {
  position: absolute;
  top: 10px;
  left: 50px;
}

.nav__link--laptops:hover {
  color: #00acdc;
  padding: 22.5px 0;
}

.main {
  grid-gap: 48px 46px;
  grid-template-rows: 1fr 1fr;
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  margin: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 778px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 408px;
  padding: 32px 16px 16px;
  transition: transform .3s;
}

.card:hover {
  transform: scale(1.2);
}

.cardTop {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.cardTop__Img {
  background-image: url("imac.202c52ff.jpeg");
  background-size: 100% 100%;
  width: 160px;
  height: 134px;
  margin: 0 19px;
}

.cardTop__h1 {
  color: #060b35;
  align-self: start;
  width: 166px;
  height: 36px;
  margin: 0;
  padding-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.cardTop__h1:hover {
  cursor: pointer;
  color: #34568b;
  transition: all .3s;
}

.cardTop__h2 {
  color: #616070;
  align-self: start;
  margin: 0;
  padding-top: 4px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.cardPrice {
  justify-content: space-between;
  width: 166px;
  height: 18px;
  display: flex;
}

.cardPrice__price-1 {
  color: #616070;
  width: 100%;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.cardPrice__price-2 {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.cardPrice-mix {
  margin-top: 24px;
}

.button__element {
  box-sizing: border-box;
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.button__element:hover {
  color: #00acdc;
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #00acdc;
  transition: all .3s;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.0aa15587.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars .stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--5 .stars__star:nth-last-child(n+2) {
  background-image: url("star-active.1a24ce14.svg");
}

.starsReviews {
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;
  width: 166px;
  margin-top: 16px;
  display: flex;
}

.reviews {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}
